
"use client";

import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet';
import { Menu, CodeXml, LogIn, LogOut, UserCog } from 'lucide-react';
import { cn } from '@/lib/utils';
import { useAuth } from '@/contexts/AuthContext';
import { auth } from '@/lib/firebase/config';
import { signOut } from 'firebase/auth';
import { useToast } from '@/hooks/use-toast';

const navLinksBase = [
  { href: '/', label: 'Home' },
  { href: '/services', label: 'Services' },
  { href: '/projects', label: 'Projects' },
  { href: '/quote-request', label: 'Request a Quote' },
];

export default function PublicHeader() {
  const pathname = usePathname();
  const router = useRouter();
  const { user, loading } = useAuth();
  const { toast } = useToast();

  const handleLogout = async () => {
    try {
      await signOut(auth);
      toast({ title: 'Logged Out', description: 'You have been successfully logged out.' });
      router.push('/'); // Redirect to home page after logout
    } catch (error) {
      console.error("Logout error:", error);
      toast({ title: 'Logout Failed', description: 'Could not log you out. Please try again.', variant: 'destructive' });
    }
  };
  
  const adminLink = { href: '/admin', label: 'Admin Panel', icon: UserCog };
  const loginLink = { href: '/login', label: 'Admin Login', icon: LogIn };

  const getNavLinks = () => {
    const links = [...navLinksBase];
    if (user) {
      links.push(adminLink);
    }
    return links;
  };
  
  const currentNavLinks = getNavLinks();

  // Do not render PublicHeader on explicit admin or login routes
  if (pathname.startsWith('/admin') || pathname === '/login') {
    return null;
  }

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border/40 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/80 shadow-md">
      <div className="container flex h-20 max-w-screen-2xl items-center justify-between">
        <Link href="/" className="flex items-center gap-2 text-primary hover:text-primary/80 transition-colors">
          <CodeXml className="h-8 w-8" />
          <span className="font-bold text-2xl">Digital Emporium</span>
        </Link>
        
        <nav className="hidden md:flex items-center space-x-4 lg:space-x-6">
          {currentNavLinks.map((link) => (
            <Link
              key={link.href}
              href={link.href}
              className={cn(
                "text-sm font-medium transition-colors duration-200 hover:text-primary",
                pathname === link.href 
                  ? "text-primary font-semibold" 
                  : "text-foreground/70"
              )}
            >
              {link.label}
            </Link>
          ))}
          {!loading && (
            user ? (
              <Button variant="outline" size="sm" onClick={handleLogout}>
                <LogOut className="mr-2 h-4 w-4" /> Logout
              </Button>
            ) : (
              <Button variant="ghost" size="sm" asChild>
                <Link href="/login">
                  <LogIn className="mr-2 h-4 w-4" /> Login
                </Link>
              </Button>
            )
          )}
        </nav>

        <div className="md:hidden flex items-center gap-2">
           {!loading && !user && (
             <Button variant="outline" size="icon" asChild className="border-primary text-primary">
                <Link href="/login" title="Admin Login">
                    <LogIn className="h-5 w-5" />
                </Link>
            </Button>
           )}
            {user && (
                 <Button variant="outline" size="icon" onClick={handleLogout} title="Logout" className="border-destructive text-destructive">
                    <LogOut className="h-5 w-5" />
                </Button>
            )}
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="outline" size="icon">
                <Menu className="h-5 w-5" />
                <span className="sr-only">Open menu</span>
              </Button>
            </SheetTrigger>
            <SheetContent side="right">
              <nav className="flex flex-col space-y-2 mt-6">
                {currentNavLinks.map((link) => (
                  <Link
                    key={link.href}
                    href={link.href}
                    className={cn(
                      "text-lg font-medium transition-colors duration-200 py-2 px-2 rounded-md",
                       pathname === link.href 
                        ? "text-primary font-semibold bg-primary/10" 
                        : "text-foreground/80 hover:text-primary hover:bg-muted"
                    )}
                  >
                     {link.icon && <link.icon className="mr-2 inline-block h-5 w-5" />}
                    {link.label}
                  </Link>
                ))}
                 <div className="pt-4 mt-4 border-t">
                 {!loading && (
                    user ? (
                    <Button variant="outline" className="w-full" onClick={handleLogout}>
                        <LogOut className="mr-2 h-5 w-5" /> Logout
                    </Button>
                    ) : (
                    <Button variant="default" className="w-full" asChild>
                        <Link href="/login">
                        <LogIn className="mr-2 h-5 w-5" /> Admin Login
                        </Link>
                    </Button>
                    )
                )}
                 </div>
              </nav>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  );
}
